import { useEffect, useRef } from "react";
import lottie from "lottie-web-light";

const CoffeeSip = () => {
  const animationContainer = useRef(null);

  useEffect(() => {
    const animation = lottie.loadAnimation({
      //@ts-ignore
      container: animationContainer.current, // Target container for the animation
      renderer: "svg", // SVG rendering
      loop: true, // Loop the animation
      autoplay: true, // Automatically play
      path: "https://lottie.host/77bc8190-122c-4736-95d8-32f3af3eeb25/O1RrKcuuMo.json", // Animation file URL
    });

    // Cleanup to prevent memory leaks
    return () => animation.destroy();
  }, []);

  return (
    <div
      ref={animationContainer}
      style={{ width: "800px", height: "800px" }}
    ></div>
  );
};

export default CoffeeSip;
